.projects {
  max-width: 100vw;
}
.projects__gallery {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, min(25vw, 30%)));
  gap: 30px;
  justify-content: center;
}
@media (max-width: 550px) {
  .projects__gallery {
    grid-template-columns: 100%;
  }
}
.projects__project {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-radius: 0.625rem;
  overflow: hidden;
  box-shadow: 0 5px 35px var(--shadow-color);
  cursor: pointer;
  transition: opacity 0.3s ease-in-out, scale 0.3s ease-in-out, filter 0.3s linear;
}
.projects__project::after {
  content: "";
  position: absolute;
  inset: 0;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.1);
  transition: opacity 0.3s linear;
}
.projects__description {
  position: absolute;
  top: 0;
  inset: 0;
  padding: 1.5rem;
  width: 100%;
  transform: translateY(-100%);
  transition: transform 0.3s;
  background-color: var(--shadow-color);
  box-shadow: 0px 0px 10px var(--shadow-color);
  backdrop-filter: blur(10px);
  color: var(--light-color);
  font-family: var(--ff-second);
  font-size: var(--fz-text);
  font-weight: 500;
  letter-spacing: 0.15em;
}
.projects__description p {
  margin-bottom: var(--gap);
}
.projects__description ul {
  padding-left: 1em;
  list-style-type: disc;
}
@media (any-hover: hover) {
  .projects__gallery:has(.projects__project:hover) .projects__project:not(:hover) {
    filter: blur(3px);
  }
  .projects__gallery:has(.projects__project:hover) .projects__project:not(:hover)::after {
    opacity: 1;
  }
  .projects__project:hover .projects__description {
    transform: translateY(0);
  }
}
.projects__image {
  padding: 1em;
  width: 100%;
}
.projects__cover {
  width: 100%;
  aspect-ratio: 1/1;
}
.projects__picture {
  width: 100%;
  border-radius: 0.625rem;
  overflow: hidden;
  box-shadow: 0px 10px 10px var(--shadow-color);
  cursor: pointer;
}
.projects__name {
  padding-inline: 1.25rem;
  padding-top: 0.9375rem;
  padding-bottom: 1.5625rem;
}
@media (any-hover: hover) {
  .projects__project:hover {
    scale: 1.05;
  }
}

.project-modal {
  max-height: 80dvh;
  overflow-y: scroll;
  margin-right: -15px;
  padding-right: 15px;
}
.project-modal::-webkit-scrollbar-thumb {
  cursor: pointer;
}
.project-modal::-webkit-scrollbar {
  width: 6px;
  height: 10px;
}
.project-modal::-webkit-scrollbar-track {
  margin-block-start: 2rem;
  background-color: var(--light-color-weak);
  border-radius: 10px;
}
.project-modal::-webkit-scrollbar-thumb {
  background-color: var(--dark-color);
  border-radius: 10px;
}
.project-modal__title {
  text-align: center;
}
.project-modal__details {
  margin-bottom: calc(var(--gap) * 1.5);
  display: grid;
  gap: var(--gap);
}
.project-modal__details-list {
  padding-bottom: 10px;
  display: flex;
  justify-content: space-evenly;
  border-bottom: 1px solid var(--light-color-weak);
}
.project-modal__details-description {
  font-family: var(--ff-second);
  font-weight: 500;
  font-size: var(--fz-text);
}
.project-modal__details-link {
  color: currentColor;
}
.project-modal__features-title {
  font-size: var(--fz-subtitle-small);
}
.project-modal__features-list {
  display: grid;
  gap: calc(var(--gap) * 1.5);
}
.project-modal__features-item {
  display: grid;
  gap: var(--gap);
}
.project-modal__features-item:not(:last-child) {
  padding-bottom: var(--gap);
  border-bottom: 1px solid var(--light-color-weak);
}
.project-modal__features-description {
  font-family: var(--ff-second);
}
.project-modal__features-image {
  width: fit-content;
  background-color: var(--light-color-weak);
  border-radius: 0.625rem;
}

.dialog {
  display: none;
  background: transparent;
  margin: auto auto;
  padding: 5px;
  width: 100%;
  gap: var(--gap);
  opacity: 0;
  translate: 0 25vh;
  transition-property: display opacity;
  transition-duration: 1s;
  transition-behavior: allow-discrete;
}
.dialog[open] {
  display: block;
  opacity: 1;
  translate: 0 0;
}
@starting-style {
  .dialog[open] {
    opacity: 0;
    translate: 0 -25vh;
  }
}
.dialog::backdrop {
  backdrop-filter: blur(10px);
}
.dialog__close {
  display: block;
  margin: 0 auto;
  margin-top: var(--gap);
  padding-block: 12px;
  padding-inline: 1em;
  background-color: var(--light-color);
  background-image: linear-gradient(45deg, var(--accent-color), var(--dark-color));
  color: var(--light-color);
  text-align: center;
  font-family: var(--ff-second);
  font-weight: 500;
  font-size: var(--fz-subtitle-small);
  transition: background-color 0.2s, color 0.2s;
}
@media (any-hover: hover) {
  .dialog__close:hover {
    background-color: var(--light-color-weak);
    color: var(--light-color-weak);
  }
}
.dialog .image {
  margin: 0 auto;
}

.project-roboto .project-modal__features-item:nth-of-type(1) {
